import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue';

export default {
	data() {
		return {
			searchVal: '',
			status: 'more',
			last_id:'',
			list:[],
			search_data:{
				search_key:'',
				page:1,
				rows:10
			},
			total:0,
			index:0
		}
	},
	components: {
		uniSearchBar
	},
	onLoad() {
		this.getList({value:this.search_data.search_key});
	},
	onPullDownRefresh() {
		
	},
	onShow() {
		
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		search (res) {
			
		},
		input (res) {
			this.searchVal = res.value;
		},
		cancel (res) {
			
		},
		onClick(res) {
			
		},
		changeGetList(res) {
			if(!res.value) this.getList(res);
		},
		
		getList(res) {
		
			if(!res) {
				//数据饱和不再进行调用
				if(this.status == 'noMore') return;
				//加载更多增加页码
				this.search_data.page++;
				this.changeStatus('loading');
			} else {
				//搜索重置页码
				this.resetPage();
				this.search_data.search_key = res.value
			}
			
			
			let data = {
				param:this.search_data
			}
			this.$ajax.post('home/get_product_list',{data: data}).then((result)=>{
				
				this.$set(this,'total',result.total);
				//无数据返回则重置状态
				if (this.total != 0 && this.list.length >= this.total ) {
					this.status = 'noMore';
					this.search_data.page -- ;
					return;
				}
				let new_list = [];
				if (this.status == 'loading') {
					//合并数据
					new_list = this.list.concat(result.product_list);
					this.changeStatus();
				} else {
					new_list = result.product_list
				}
				this.$set(this,'list',new_list);
				
			});
			
		},
		resetPage () {
			this.search_data.page = 1;
			//重新搜索返回页面顶部
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			});
			this.changeStatus();
			
		},
		changeStatus (status) {
			this.status = status||'more';
		},
		toDetail(res,index) {
			this.index = index;
			let _this = this;
			this.$eventHub.$on('product_detail', function(data) {
				_this.$set(_this.list,_this.index,data);
				
				
				//清除监听，不清除会消耗资源
				_this.$eventHub.$off('product_detail');
			});

			this.$router.push("/pages/product-detail/index",{
				product_detail:res
			})
			
		}

	}
}

